@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Permanent+Marker&family=Raleway&display=swap);
*{
    font-family: 'Raleway',sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color:white;
}
html{
    font-size: 62.5%;
}
body{
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}
::-webkit-scrollbar{
    background-color: transparent; 
  }
  ::-webkit-scrollbar-thumb{
    background-color: rgba(0, 0, 0, 0.349); 
    border-radius: 10px;
  }
